import React from 'react';
import { Link } from 'react-router-dom';

export const TicketsArticle = () => {
    return (
        <>
            <div className="col col-md-3"></div>
            <div className="col-12 col-md-6 mt-2 article">
                <h1 class="h3 text-success mb-4"> Apps para ahorrar con tickets </h1>
                <h2 class="h4 text-success mb-4"> Recibe dinero por tickets de supermercado </h2>
                <p class="text-justify">
                    En esta seccin os enseo como ganar dinero o ahorrar por hacer la compra en cualquier supermercado ,
                    adems de dar mi opinin sobre las apps, que permiten esto 
                </p>
                <div class="card mb-3">
                    <div class="card-body">
                        <h3 class="h4 text-danger"> Promos App: Cupones vlidos en cualquier supermercado</h3>
                        <p class="text-justify">
                            Promos es una app con la que podemos ganar dinero por comprar en cualquier supermercado.
                            Es una de las mejores porque hay mucha variedad de promociones y es muy intuitiva 
                        </p>
                        <Link
                            class="btn btn-outline-success mb-4"
                            exact
                            to="/promos-app/" 
                            title="Mira ms informacin de Promos">
                                Mira ms informacin de Promos 
                        </Link>
                    </div>
                </div>
                <div class="card mb-3">
                    <div class="card-body">
                        <h3 class="h4 text-danger"> Tiendeo: Catlogo de supermercados, cashback y mucho MS</h3>
                        <p class="text-justify">
                            Tiendeo es una app con la que podemos ver el catlogo de los supermercados, 
                            subir tickets de la compra y ganar dinero con ellos, entre otras cosas. <br /><br />

                            Solo por registrarse puedes ganar 1 GRATIS + 0.50 por subir el primer ticket. <br /><br />

                            Quieres recibir cashback por tus compras en tiendeo? <br />
                            Pulsa abajo para ver ms informacin y registrarte 
                        </p>
                        <Link 
                            class="btn btn-outline-success mb-4"
                            exact
                            to="/tiendeo/" 
                            title="Mira ms informacin de Tiendeo">
                                Mira ms informacin de Tiendeo 
                        </Link>
                    </div>
                </div>
            </div>
            <div class="col col-md-3 mb-5">
                <div class="card">
                    <div class="card-body">
                        <h2 class="h5 text-danger">Cmo ahorrar con los tickets de compra?</h2>
                        <p class="text-justify">
                            En esta seccin dejo algunas apps con las que subir los tickets del supermercado y ahorrar dinero.
                            Es muy fcil, solo hace descargarse alguna app, registrarse, elegir los productos que hemos comprado. <br /><br />
                            (En todas las apps, se actualizan los productos cada semana)
                        </p>
                    </div>
                </div>
            </div>
        </>
    )
}